import { mapGetters } from 'vuex'
import { mapState } from 'vuex'
export default {
    data() {
        return {
            dataList:[],
            isZH:true,
            carList:[],
            ftotal:0
        }
    },
    created:function () {
        ///
        if(localStorage.LANGUAGE!='zh'){
            this.isZH = false;
        }
        let _this = this;
        $(function(){
            $(".navbar-center").css('marginLeft',0);
            //
            let params = {
                hotelid: localStorage.HOTELID,
                status: 0,
                parentid:0,
                withchild:1
            }
            _this.$store.dispatch('getFirstTags', params).then(function (res) {
                _this.dataList = res.data.list;
            });
        });
    },
    methods: {
        orderClose:function(){
            $("#section2").show();
            $("#section3").hide();
        },
        buyClose:function(){
            $("#section1").show();
            $("#section2").hide();
            $(".side-bar").show();
        },
        buyCreate: function() {
            $("#section1").hide();
            $("#section2").show();
            $(".side-bar").hide();
        },
        goBack:function(){
            if(this.preRoute){
                this.$router.push('/'+this.preRoute);
            }else{
                this.$router.push('/home');
            }
        },
        apply: function() {
            let dialog = window.YDUI.dialog;
            let alobj = new alertLanguage();
            let obj = alobj.getAlertMsg(localStorage.LANGUAGE);
            let title = obj.title;
            let sureBnt = obj.sureBnt;
            let msg = '';
            if(localStorage.LANGUAGE!='zh'){
                msg = 'Please select product';
            }
            let _this = this;
            if(global.shopCar.size==0){
                dialog.confirm(title,msg, [
                    {
                        txt: sureBnt,
                        color: false,
                        callback: function () {
                            _this.caculate();
                        }
                    }
                ]);
            }else{
                $("#section3").show();
                $("#section2").hide();
            }
        },
        orderApply: function() {
            let alobj = new alertLanguage();
            let obj = alobj.getAlertMsg(localStorage.LANGUAGE);
            let title = obj.title;
            let msg = obj.shopping.msg;
            let sureBnt = obj.sureBnt;
            let cancelBnt = obj.cancelBnt;

            let _this = this;
            let dialog = window.YDUI.dialog;
            dialog.confirm(title,msg, [
                {
                    txt: sureBnt,
                    color: false,
                    callback: function () {
                        _this.doOrder();
                    }
                },
                {
                    txt: cancelBnt,
                    color: false,
                    callback: function () {

                    }
                }
            ]);
        },
        doOrder: function(){
            $(".buy_foot button").attr("disabled",true);
            $(".buy_foot button").css({background: "grey"});
            //
            let arr = [];
            for(let i = 0;i < this.this.carList.length;i++){
                for(let j = 0;i < this.this.carList[i].p.c;j++){
                    let obj = {"id":0,"num":0};
                    obj.id = this.this.carList[i].p.c[j].id;
                    obj.num = this.this.carList[i].p.c[j].num;
                    arr.push(pobj);
                }
            }
            let _this = this;
            let params = {
                hotelid: localStorage.HOTELID,
                userid: localStorage.userId,
                products: arr
            };
            this.$store.dispatch("addShoppingCart", params).then(res => {
                let mymsg = "";
                ///
                if(localStorage.LANGUAGE!='zh'){
                    mymsg = "success";
                }
                if (res.data.code == 0) {
                    mymsg = this.language.msg.buy_info;
                } else {
                    mymsg = res.data.msg;
                }
                $(".buy_foot button").attr("disabled",false);
                $(".buy_foot button").css({background: "#f0c366"});
                //
                let alobj = new alertLanguage();
                let obj = alobj.getAlertMsg(localStorage.LANGUAGE);
                let title = obj.title;
                let msg = obj.shopping.msg;
                let sureBnt = obj.sureBnt;
                let dialog = window.YDUI.dialog;
                dialog.confirm(title,mymsg, [
                    {
                        txt: sureBnt,
                        color: false,
                        callback: function () {
                            global.shopCar.clear();
                            $("#section1").show();
                            $("#section3").hide();
                            $(".side-bar").show();
                        }
                    }
                ]);
            });
        },
        toBuy: function(){
            $("#section3").show();
            $("#section2").hide();
        },
        goDetail: function(firstTag,sId,isRobot) {
            let fId = firstTag.id;
            localStorage.NEWTYPE=fId+','+sId;
            global.firstTag=firstTag;
            let p = "/newshopping";
            if(isRobot==0){
                p = "/robotDelivery";
            }
            this.$router.push({
                path: p,
                query: {pageFlag:"home"}
            });
        },
        robotWash: function() {
             let dialog = window.YDUI.dialog;
             let alobj = new alertLanguage();
             let obj = alobj.getAlertMsg(localStorage.LANGUAGE);
             let title = obj.title;
             let sureBnt = obj.sureBnt;
             let cancelBnt = obj.cancelBnt;
             let msg = obj.washMsg;
             let _this = this;
             dialog.confirm(title,msg, [
                    {
                        txt: sureBnt,
                        color: false,
                        callback: function () {
                            let params = {
                                token: localStorage.TOKEN//to: 2206
                            };
                            //RobotSend
                            _this.$store.dispatch('RobotSend', params).then(function (res) {
                                msg = obj.successMsg;
                                if(res.data.code!=0){
                                    msg = obj.sytemBusy;
                                }
                                dialog.confirm(title,msg, [
                                    {
                                        txt: sureBnt,
                                        color: false,
                                        callback: function () {
                                            
                                        }
                                    }
                                ]);
                            });
                        }
                    },
                    {
                        txt: cancelBnt,
                        color: false,
                        callback: function () {

                        }
                    }
             ]);
        },
        goShopCar: function() {
            this.buyCreate();
            this.caculate();
        },
        caculate: function() {
            if(global.shopCar===undefined){
                //
                global.shopCar = new Map();
            }
            let total = 0;
            let arr = [];
            global.shopCar.forEach(function(value, key, map){
                let pobj = {p:{id:value.p.id,title_lang1:value.p.title_lang1,title_lang2:value.p.title_lang2,c:[]}};
                value.c.forEach(function(v2, k2, m2){
                    let cobj = {id:v2.c.id,title_lang1:v2.c.title_lang1,title_lang2:v2.c.title_lang2,img:v2.c.pic,num:v2.num,price:v2.c.price};
                    pobj.p.c.push(cobj);
                    total = total + cobj.num * cobj.price;
                });
                arr.push(pobj);
            });
            this.carList = arr;
            this.ftotal = total;
        },
        clearCar: function() {
            global.shopCar.clear();
            let dialog = window.YDUI.dialog;
            let alobj = new alertLanguage();
            let obj = alobj.getAlertMsg(localStorage.LANGUAGE);
            let title = obj.title;
            let sureBnt = obj.sureBnt;
            let msg = '';
            if(localStorage.LANGUAGE!='zh'){
                msg = 'Shopping cart is empty';
            }
            let _this = this;
            dialog.confirm(title,msg, [
                {
                    txt: sureBnt,
                    color: false,
                    callback: function () {
                        _this.caculate();
                    }
                }
            ]);
        },
        addProduct: function(pid,cid) {
            let pshopCar = global.shopCar.get('p'+pid);
            let cshopCar = pshopCar.c.get('c'+cid);
            cshopCar.num = cshopCar.num + 1;
            this.caculate();
        },
        delProduct: function(pid,cid) {
            let pshopCar = global.shopCar.get('p'+pid);
            let cshopCar = pshopCar.c.get('c'+cid);
            if(cshopCar.num==1){
                pshopCar.c.delete('c'+cid);
            }else{
                cshopCar.num = cshopCar.num - 1;
            }
            if(pshopCar.c.size==0){
                global.shopCar.delete('p'+pid);
            }
            this.caculate();
        },
        delItem: function(pid,cid) {
            let pshopCar = global.shopCar.get('p'+pid);
            let cshopCar = pshopCar.c.get('c'+cid);
            pshopCar.c.delete('c'+cid);
            if(pshopCar.c.size==0){
                global.shopCar.delete('p'+pid);
            }
            this.caculate();
        }
    },
    mounted:function () {
        //falg
        isHomePage(0)
    },
    components: {
    },
    computed: {
        ...mapState({
            language: state => state.language.language
        })
    },
};



