import { mapGetters } from 'vuex'
import { mapState } from 'vuex'
export default {
    data() {
        return {
            dataList: [],
            details: [],
            noData: false,
            isZH:true,
            title:'',
            orderNo:'',
            orderDate:'',
            numStr:''
        }
    },
    created:function () {
        ///
        if(localStorage.LANGUAGE!='zh'){
            this.title = "Orders";
            this.numStr = "Quantity";
        }
        let _this = this
        $(function(){
            $(".navbar-center").css('marginLeft',0);
            //
            let params = {
                token: localStorage.TOKEN,
                lang:localStorage.LANGUAGE,
                hotelid:localStorage.HOTELID
            }
            this.$store.dispatch('viewOrder', params).then(function (res) {
                if(res.code == 0){
                    _this.dataList = res.data;
                    for(let i=0;i<_this.dataList.length;i++){
                         // 
                        let date = new Date(Number(_this.dataList[i].created_at));
                        let Y = date.getFullYear() + '-';
                        let M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
                        let D = date.getDate() + ' ';
                        let h = date.getHours() + ':';
                        let m = date.getMinutes() + ':';
                        let s = date.getSeconds();
                        let dateStr = Y+M+D+h+m+s;
                        _this.dataList[i].created_at = dateStr;
                    }
                } else {
                    _this.$dialog.toast({mes: res.msg, timeout: 1000});
                }
            });
        });
    },
    methods: {
        close:function(){
            $("#section2").hide();
            $("#section1").show();
        },
        goBack:function(){
            this.$router.push('/home');
        },
        detail:function(order){
            $("#section1").hide();
            $("#section2").show();
            this.orderNo = order.id;
            this.orderDate = order.created_at;
            this.details = order.products;
        }
    },
    mounted:function () {
        //falg
        isHomePage(0)
    },
    components: {
    },
    computed: {
        ...mapState({
            language: state => state.language.language
        })
    },
};



